      <button className="w-full bg-indigo-900/40 hover:bg-indigo-900/60 text-indigo-100 font-bold py-3 rounded-xl border border-indigo-500/20 transition-all flex items-center justify-center gap-2">
        <Save size={18} />
        Save to Cloud
      </button>
