export default function Home() {
  const [pages, setPages] = useState([
    { type: 'title', title: 'title', description: 'description' },
    { type: 'grid', cards: mockCards.slice(0, 9), gridSize: 9 }
  ]);
  const [currentPageIndex, setCurrentPageIndex] = useState(0); // This is the left page in spread
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [currentAddingPageIndex, setCurrentAddingPageIndex] = useState(null);

  const handleTitleChange = (newTitle) => {
    const newPages = [...pages];
    newPages[0].title = newTitle;
    setPages(newPages);
  };

  const handleDescChange = (newDesc) => {
    const newPages = [...pages];
    newPages[0].description = newDesc;
    setPages(newPages);
  };

  const handleSlotClick = (pageIdx) => {
    setCurrentAddingPageIndex(pageIdx);
    setIsModalOpen(true);
  };

  const handleAddCards = (newCards) => {
    if (currentAddingPageIndex === null) return;
    const newPages = [...pages];
    newPages[currentAddingPageIndex].cards = [...newPages[currentAddingPageIndex].cards, ...newCards];
    setPages(newPages);
  };

  const addPage = () => {
    setPages([...pages, { type: 'grid', cards: [], gridSize: 9 }]);
  };

  const nextPage = () => {
    if (currentPageIndex + 2 < pages.length) {
      setCurrentPageIndex(currentPageIndex + 2);
    }
  };

  const prevPage = () => {
    if (currentPageIndex - 2 >= 0) {
      setCurrentPageIndex(currentPageIndex - 2);
    }
  };

  return (
    <div className="h-screen bg-[#070709] text-zinc-200 flex overflow-hidden">
      <AddCardModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onAddCard={handleAddCards}
      />

      {/* Far Left Navigation */}
      <nav className="w-16 border-r border-white/5 flex flex-col items-center py-6 gap-6 z-50 bg-[#070709]">
        <div className="w-8 h-8 bg-indigo-600 rounded-lg flex items-center justify-center text-white font-black text-xl mb-4">P</div>
        <LeftNavItem icon={Plus} active />
        <LeftNavItem icon={Edit3} />
        <LeftNavItem icon={Compass} />
        <LeftNavItem icon={Grid} />
        <LeftNavItem icon={Search} />
        <LeftNavItem icon={ImageIcon} />
        <LeftNavItem icon={Printer} />
        <LeftNavItem icon={Share2} />
        <div className="mt-auto space-y-6 flex flex-col items-center">
          <LeftNavItem icon={Trash2} />
          <LeftNavItem icon={User} />
        </div>
      </nav>

      {/* Main Binder Area */}
      <main className="flex-1 relative flex flex-col items-center justify-center p-4 md:p-12 overflow-hidden bg-black">

        {/* Binder Controls (Top) */}
        <div className="absolute top-8 flex items-center gap-4 text-xs font-bold text-zinc-500 z-50">
          <div className="flex items-center gap-1 bg-zinc-900/80 px-3 py-1.5 rounded-lg border border-zinc-800">
            <Layout size={14} className="text-zinc-400" />
            <span>Customize Cover</span>
          </div>
          <div className="flex items-center gap-2">
            <button onClick={prevPage} className="p-1 hover:text-white transition-colors disabled:opacity-30" disabled={currentPageIndex === 0}>
              <ChevronLeft size={20} />
            </button>
            <span className="text-white w-8 text-center">{currentPageIndex + 1} / {pages.length}</span>
            <button onClick={nextPage} className="p-1 hover:text-white transition-colors disabled:opacity-30" disabled={currentPageIndex + 2 >= pages.length}>
              <ChevronRight size={20} />
            </button>
          </div>
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1 bg-zinc-900/80 px-3 py-1.5 rounded-lg border border-zinc-800">
              <Layout size={14} className="text-zinc-400" />
              <span>Tray</span>
              <ChevronRight size={14} className="rotate-90" />
            </div>
            <div className="flex items-center gap-1 bg-zinc-900/80 px-3 py-1.5 rounded-lg border border-zinc-800">
              <div className="w-3 h-3 bg-indigo-500 rounded-sm" />
              <span>Color Match</span>
              <ChevronRight size={14} className="rotate-90" />
            </div>
          </div>
        </div>

        {/* The Binder Spread */}
        <div className="w-full max-w-6xl aspect-[1.4/1] flex relative group">

          {/* Left Page Shadow */}
          <div className="absolute inset-y-4 -left-4 w-8 bg-black/40 blur-2xl rounded-full z-0" />

          {/* Left Page */}
          <div className="flex-1 bg-zinc-900 border-y border-l border-white/5 rounded-l-[2.5rem] overflow-hidden relative shadow-2xl z-10">
            <AnimatePresence mode="wait">
              <motion.div
                key={`page-${currentPageIndex}`}
                initial={{ x: 20, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -20, opacity: 0 }}
                className="h-full"
              >
                {pages[currentPageIndex]?.type === 'title' ? (
                  <TitlePage
                    title={pages[currentPageIndex].title}
                    description={pages[currentPageIndex].description}
                    onTitleChange={handleTitleChange}
                    onDescChange={handleDescChange}
                  />
                ) : (
                  <GridPage
                    cards={pages[currentPageIndex]?.cards}
                    gridSize={pages[currentPageIndex]?.gridSize}
                    onSlotClick={() => handleSlotClick(currentPageIndex)}
                  />
                )}
              </motion.div>
            </AnimatePresence>
          </div>

          {/* Spine */}
          <div className="w-10 flex flex-col justify-between py-12 bg-[#1a1a1e] relative z-20 shadow-[inset_0_0_15px_rgba(0,0,0,0.8)] border-y border-white/5">
            {[1, 2, 3, 4, 5, 6].map(i => (
              <div key={i} className="h-0.5 w-full bg-white/5 shadow-sm" />
            ))}
            {/* The actual rings/holes could be styled better here */}
            <div className="absolute inset-0 flex flex-col justify-around items-center py-20 pointer-events-none">
              {[1, 2, 3].map(i => (
                <div key={i} className="w-6 h-1 bg-zinc-800 rounded-full shadow-inner border border-white/5" />
              ))}
            </div>
          </div>

          {/* Right Page */}
          <div className="flex-1 bg-zinc-900 border-y border-r border-white/5 rounded-r-[2.5rem] overflow-hidden relative shadow-2xl z-10">
            <AnimatePresence mode="wait">
              <motion.div
                key={`page-${currentPageIndex + 1}`}
                initial={{ x: -20, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: 20, opacity: 0 }}
                className="h-full"
              >
                {pages[currentPageIndex + 1] ? (
                  <GridPage
                    cards={pages[currentPageIndex + 1].cards}
                    gridSize={pages[currentPageIndex + 1].gridSize}
                    onSlotClick={() => handleSlotClick(currentPageIndex + 1)}
                  />
                ) : (
                  <div className="flex items-center justify-center h-full bg-zinc-900/30">
                    <button
                      onClick={addPage}
                      className="p-8 rounded-full bg-zinc-800/50 text-zinc-600 hover:text-white hover:bg-zinc-800 transition-all border-4 border-dashed border-zinc-700/50"
                    >
                      <Plus size={48} />
                    </button>
                  </div>
                )}
              </motion.div>
            </AnimatePresence>
          </div>

          {/* Floating Navigation Controls */}
          <button
            disabled={currentPageIndex === 0}
            onClick={prevPage}
            className="absolute left-[-2rem] top-1/2 -translate-y-1/2 w-12 h-12 rounded-full bg-zinc-800/80 border border-zinc-700 text-white flex items-center justify-center hover:bg-zinc-700 transition-all z-30 disabled:opacity-0"
          >
            <ChevronLeft size={24} />
          </button>

          {/* The + Button on the right requested by user */}
          <button
            onClick={addPage}
            className="absolute right-[-4.5rem] top-1/2 -translate-y-1/2 w-14 h-14 rounded-2xl bg-indigo-600 text-white flex items-center justify-center shadow-[0_0_20px_rgba(79,70,229,0.4)] hover:scale-110 active:scale-95 transition-all z-30"
          >
            <Plus size={32} />
          </button>
        </div>

        {/* Display Current Pages Indicator (Bottom) */}
        <div className="mt-12 text-[10px] text-zinc-600 uppercase tracking-[0.2em] font-bold">
          Page {currentPageIndex + 1} - {currentPageIndex + 2} of {pages.length}
        </div>
      </main>

      {/* Right Settings Sidebar */}
      <SettingsPanel pages={pages} currentPageIndex={currentPageIndex} />
    </div>
  );
}
